import React from "react";
import { connect } from "react-redux";
import { setLoading, setError } from "../../redux/actions";

const ExceptionPage = (props) => {
  const { error } = props;
  React.useEffect(() => {
    setError("");
  }, []);
  return <>{error}</>;
};

const mapStateToProps = (state) => {
  return {
    error: state.app.error,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    // actGetOnboard: (body, action) => () => {},
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ExceptionPage);
